import { gql } from '@apollo/client';

export const LISTPRODUCTS = gql`
  query listProducts(
    $filter: ProductFilterInput
    $order: [ProductSortInput!]
    $skip: Int
    $take: Int
  ) {
    listProducts(filter: $filter, order: $order, skip: $skip, take: $take) {
      pageInfo {
        hasNextPage
        hasPreviousPage
      }
      totalCount
      items {
        id
        photo
        sku
        title
        slug
        description
        projectId
        createdAt
        updatedAt
        shopType
      }
    }
  }
`;
